export default {
	data() {
		return {
			name: '赵鸿飞',
			Data:[],
			add:''
		}
	},
	onLoad: function(option) { //option为object类型，会序列化上个页面传递的参数
		//       console.log(option.item); //打印出上个家伙传递的参数。
		// this.name = option.item
	},
	created() {
		uni.showLoading({
		    title: '加载中'
		});
		uni.request({
			url: this.$store.state.url + 'System/GetReturnVisit',
			method: 'GET',
			data: {
				search : ''
			},
			header: {
				'Token': this.$store.state.token
			},
			success: (res) => {
				console.log(JSON.parse(res.data).Data)
				this.Data = JSON.parse(res.data).Data
				setTimeout(function () {
				    uni.hideLoading();
				}, 100);
			}
		});
		


	},
	methods: {
		sou(){
			uni.request({
				url: this.$store.state.url + '/System/GetReturnVisit',
				method: 'GET',
				data: {
					search : this.add
				},
				header: {
					'Token': this.$store.state.token
				},
				success: (res) => {
					console.log(JSON.parse(res.data).Data)
					this.Data = JSON.parse(res.data).Data
				}
			});
			
		},
		VIP(item){
			
			this.$store.state.vipitem = item
			this.$store.state.vip = true
			uni.navigateTo({
			    url: "/pages/home/works/detailed/Add_order",
			});

		}
	}
}
